
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.wrapper {
  max-width: 1000px;
  margin: auto;
  display: grid;
  grid-gap: 1em;
  grid-template-columns: 1fr 2fr;
  grid-template-areas:
  "header header"
  "controls main"
}
.header {
  grid-area: header;
  padding-bottom: 20px;
  border-bottom: 1px solid #d8d8d8;
  position: relative;
}
.header h2 {
  font-size: 2em;
  margin: 0;
}
.version {
  position: absolute;
  right: 0;
  bottom: 0;
  padding: 1em;
}
.main {
  grid-area: main;
  height: 600px;
}
.controls {
  grid-area: controls;
  display: flex;
  background: #EEE;
  flex-direction: column;
  box-shadow: 2px 2px 3px #bababa;
}
.controls > span {
  margin: 1em 0;
  font-weight: 700;
}
.wrapper > div {
  padding: 1em;
}
.controls button {
  margin-bottom: 1em;
}
@media only screen and (min-device-width : 320px) and (max-device-width : 480px) {
  .wrapper {
    grid-template-areas:
    "header header"
    "main main"
    "controls controls"
  }
  .main {
    height: initial;
  }
  .version {
    top: 0;
    bottom: initial;
  }
  .controls {
    margin: 1em;
  }
  .id {
    display: flex;
    margin: auto;
  }
  }